@keyframes slideBottom {
  0% {
    transform: translateY(100%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

body,
html {
  font-family: "Raleway", sans-serif;
  font-size: 15px;
  font-weight: 500;
  line-height: 22.5px;
  color: #1C1E20;
}

* {
  box-sizing: border-box;
}

*:focus {
  outline: none;
}

a {
  text-decoration: none;
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 + p {
  margin-top: 0;
}

p {
  margin: 15px 0;
}

img {
  max-width: 100%;
  width: 100%;
  object-fit: cover;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
}

svg {
  transition: .3s;
}

button {
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

section {
  padding: 100px 0;
}

@media (max-width: 768px) {
  section {
    padding: 70px 0;
  }
}

@media (max-width: 425px) {
  section {
    padding: 50px 0;
  }
}

@media (max-width: 320px) {
  section {
    padding: 30px 0;
  }
}

.container {
  max-width: 1232px;
  padding: 0 16px;
  margin: 0 auto;
}

.container-fluid {
  max-width: 100%;
  padding: 0;
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  background-color: #ffffff;
  border: 1px solid #C7C7C7;
  font-size: 15px;
  line-height: 18px;
  color: #1C1E20;
  font-weight: 700;
  text-transform: capitalize;
  font-family: "Raleway", sans-serif;
  padding: 0 15px;
  white-space: nowrap;
  border-radius: 20px;
}

.btn-purple {
  background: #4D1BC1;
  color: #ffffff;
  border: 1px solid #4D1BC1;
}

.breadcrumb__list {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.breadcrumb__list li {
  position: relative;
  padding-right: 15px;
  margin-right: 5px;
  line-height: 16.9px;
}

.breadcrumb__list li:after {
  position: absolute;
  content: '/';
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  color: #1C1E20;
}

.breadcrumb__list li:last-child:after {
  display: none;
}

.breadcrumb__link {
  color: #1C1E20;
  font-size: 13px;
  font-weight: 400;
  text-transform: capitalize;
  line-height: 16.9px;
}

.anim-items img {
  transition-delay: .6s;
  transition: all .8s linear;
  transition: 1.2s;
  will-change: transform;
}

.row {
  display: flex;
  justify-content: space-between;
  margin-top: 100px;
}

h5 {
  font-size: 25px;
  background-color: #fff;
}

p {
  background-color: yellow;
}

.column {
  width: calc(100% / 4);
  padding: 20px;
  background-color: green;
}
